import React, { useState } from 'react'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import { useModal } from '../../../hooks/basic/useModal';
import { styled } from '@mui/system';
import { Box as MUIBox } from '@mui/material'
import { useMutation } from '@tanstack/react-query';
import ModalContainerSub from '../../layout/ModalContainerSub';
import ProfileImage from '../../UI/Atoms/Profile/ProfileImage';
import { getSabumInfo } from '../../../api/Sabum/sabum';
import { useDebug } from '../../../hooks/basic/useDebug';
import { useRecoilValue } from 'recoil';
import dojangIdAtom from '../../../recoil/atom/dojangIdAtom';
import { getKwanjangInfo } from '../../../api/Kwanjang/kwanjang';
import InfoLabelSM from '../../UI/Atoms/Info/InfoLabelSM';
import InfoLabelMD from '../../UI/Atoms/Info/InfoLabelMD';
import InfoData from '../../UI/Atoms/Info/InfoData';
import Box from '../../layout/Box';
import theme from '../../../themes';
import { phoneHyphenRegex } from '../../../utils/lib/Regex';

const StyledBox = styled(MUIBox)(({ theme }) => ({
  display: 'flex',
  gap: '0.5rem',
  border: '1px solid #dddddd',
  borderRadius: '5px',
  height: '60px',
  backgroundColor: theme.palette.background.secondary,
  // justifyContent: 'space-between',
  alignItems: 'center',
  padding: '0 10px',
  cursor: 'pointer',
  '&:hover': {
    backgroundColor: theme.button.box.hover,
    boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  },
  '&:active': {
    backgroundColor: theme.button.box.active,
    boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  },
}));



const TeacherPop = ({ info, kwanjang }) => {
  const dojangId = useRecoilValue(dojangIdAtom)

  const sabumId = info?.InstructorClasses?.sabum || ''
  const [sabumInfo, setSabumInfo] = useState()
  const kwanjangId = info?.InInstructorClasses?.kwanjang || ''
  const [kwanjangInfo, setKwanjangInfo] = useState()

  // custom hook
  const [open, openFn, closeFn] = useModal()
  const [openKJ, openKJFn, closeKJFn] = useModal()

  // query
  const sabum = useMutation((sabumId) => getSabumInfo(dojangId, sabumId))
  const kj = useMutation((kwanjangId) => getKwanjangInfo(kwanjangId))

  // handler
  const handleClick = (e) => {
    if (kwanjang) {
      kj.mutate(kwanjangId, {
        onSuccess: (res) => {
          setKwanjangInfo(res)
          openKJFn()
        }
      })

    } else {
      sabum.mutateAsync(sabumId, {
        onSuccess: (res) => {
          setSabumInfo(res)
          openFn()
        }
      })
    }
  }


  return (
    <>
      <StyledBox onClick={handleClick}>
        <ProfileImage urlKey={info?.photo_url} />
        <div className="text-[16px] w-[162px]">
          {info?.last_name}
          {info?.first_name} {kwanjang ? '관장님' : '사범님'}
        </div>
        <div className="flex jsutify-center items-center">
          <ArrowForwardIosIcon fontSize="10px" className="text-[#555555]" />
        </div>
      </StyledBox>

      <ModalContainerSub open={open} onClose={() => closeFn()}>
        <div className="text-[22px] font-bold">사범님 정보</div>
        <div className="flex flex-col items-center mt-[30px]">
          <ProfileImage size="140px" urlKey={sabumInfo?.photo_url} />
          <div className="mt-[10px] font-bold text-[18px]">
            {sabumInfo?.last_name}
            {sabumInfo?.first_name}
          </div>
          <div className="text-[#00196a] mb-[2px]">{sabumInfo?.dob}</div>
        </div>
        <Box className="pt-[0]">
          <div className="flex items-center mb-[5px]">
            <InfoLabelMD className="text-[16px]">담당반</InfoLabelMD>
            <InfoData>
              {sabumInfo &&
                sabumInfo?.Classes?.map((item, idx) => {
                  {
                    if (idx === 0) {
                      return `${item?.title}`;
                    } else {
                      return `, ${item?.title}`;
                    }
                  }
                })}
            </InfoData>
          </div>
          <div className="flex items-center mb-[5px]">
            <InfoLabelMD className="text-[16px]">전화번호</InfoLabelMD>
            <InfoData>{phoneHyphenRegex(sabumInfo?.phone_number)}</InfoData>
          </div>
          <div className="flex items-center mb-[5px]">
            <InfoLabelMD className="text-[16px]">단/급 정보</InfoLabelMD>
            <InfoData>{sabumInfo?.level}</InfoData>
          </div>
          <div className="flex items-center mb-[5px]">
            <InfoLabelMD className="text-[16px]">입사일</InfoLabelMD>
            <InfoData>{sabumInfo?.dojang_reg_date}</InfoData>
          </div>
        </Box>
      </ModalContainerSub>

      <ModalContainerSub open={openKJ} onClose={() => closeKJFn()}>
        <div className="text-[22px] font-bold">관장 정보</div>
        <div className="flex flex-col items-center mt-[30px]">
          <ProfileImage size="140px" urlKey={kwanjangInfo?.photo_url} />
          <div className="mt-[10px] font-bold text-[18px]">
            {kwanjangInfo?.last_name}
            {kwanjangInfo?.first_name}
          </div>
          <div className="text-[#00196a] mb-[2px]">{kwanjangInfo?.dob}</div>
        </div>
        <Box className="pt-[0]">
          <div className="flex items-center mb-[5px]">
            <InfoLabelMD>담당반</InfoLabelMD>
            <InfoData>
              {kwanjangInfo &&
                kwanjangInfo?.Classes?.map((item, idx) => {
                  {
                    if (idx === 0) {
                      return `${item?.title}`;
                    } else {
                      return `, ${item?.title}`;
                    }
                  }
                })}
            </InfoData>
          </div>
          <div className="flex items-center mb-[5px]">
            <InfoLabelMD>전화번호</InfoLabelMD>
            <InfoData>{phoneHyphenRegex(kwanjangInfo?.phone_number)}</InfoData>
          </div>
          <div className="flex items-center mb-[5px]">
            <InfoLabelMD>단/급 정보</InfoLabelMD>
            <InfoData>{kwanjangInfo?.level}</InfoData>
          </div>
        </Box>
      </ModalContainerSub>
    </>
  );
}

export default TeacherPop