import { useMutation } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilState } from 'recoil'
import { updateClass } from '../../../../../../api/Class/ClassApi'
import ClassSchedule from '../../../../../../components/common/Table/ClassSchedule'
import Box from '../../../../../../components/layout/Box'
import BlackBtnSM from '../../../../../../components/UI/Atoms/Button/BlackBtnSM'
import EditBtnSM from '../../../../../../components/UI/Atoms/Button/EditBtnSM'
import WhiteBtnSM from '../../../../../../components/UI/Atoms/Button/WhiteBtnSM'
import InfoLabelSM from '../../../../../../components/UI/Atoms/Info/InfoLabelSM'
import { classInfoAtom } from '../../recoil/classInfoAtom'

const ScheduleInfo = () => {
  const [edit, setEdit] = useState(false)

  // recoil
  const [classInfo, setClassInfo] = useRecoilState(classInfoAtom)

  const [editData, setEditData] = useState(classInfo)

  // query
  const post = useMutation((body) => updateClass(classInfo?.id, body), {
    onSuccess: () => setEdit(false)
  })

  // handler
  const handleCancle = () => {
    setEditData({...classInfo})
    setEdit(false)
  }
  const handleSave = async () => {
    if(editData?.title === '') {
      alert('반 이름은 비워둘 수 없습니다.')
      return
    }
    if(
      !editData?.mon_time &&
      !editData?.tue_time &&
      !editData?.wed_time &&
      !editData?.thu_time &&
      !editData?.fri_time &&
      !editData?.sat_time &&
      !editData?.sun_time
    ) {
      alert('최소 1개 이상의 시간표를 입력해주세요.')
      return
    }
    post.mutate(editData)
  }

  const handleChange = (e) => {
    const { name, value } = e.target
    setEditData({...editData, [name]:value})
  }

  return (
    <>
      <Box
        title='시간표'
        btn={ edit ?
          <div className='flex gap-[5px]'>
            <WhiteBtnSM onClick={handleCancle}>취소</WhiteBtnSM>
            <BlackBtnSM onClick={handleSave}>저장</BlackBtnSM>
          </div>
        :
          <EditBtnSM onClick={()=>setEdit(true)}>수정</EditBtnSM>
        }
      >
        <div className='mb-[10px] flex justify-start'>
          <InfoLabelSM className="w-[88px] flex-none pt-[5px]">반 이름</InfoLabelSM> 
          {edit &&
            <input 
              name='title' 
              value={editData?.title || ''}
              onChange={handleChange} 
              disabled={!edit} 
              className="px-[10px] w-[186px] h-[30px] border-[1px] border-solid bg-[#fff] text-[14px]" 
            />
          }
          {!edit &&
            <input 
            name='title' 
            value={editData?.title || ''}
            onChange={handleChange} 
            disabled={!edit} 
            className="px-[10px] w-[186px] h-[30px] border-x-0 border-t-0 border-b-[1px] bg-[#fff] text-[14px]" 
          />
          }
        </div>

        <ClassSchedule data={editData} setData={setEditData} edit={edit} />
      </Box>
    </>
  )
}

export default ScheduleInfo