import authApi from '../authApi'

export const getClassList = async (dojangId) => {
  const res = await authApi.get(`class/classreadforid/${dojangId}`)
  return res.data
}

export const getClassInfo = async (classId, dojangId) => {
  const res = await authApi.get(`class/classread/${dojangId}/${classId}`)
  return res.data[0]
}

export const getClassInfoList = async (dojangId) => {
  const res = await authApi.get(`class/classreadbyday/${dojangId}/all`)
  return res.data
}

export const deleteClass = async (classId) => {
  const res = await authApi.delete(`class/classdelete/${classId}`)
  return res
}

export const updateClass = async (classId, body) => {
  const res = await authApi.post(`class/classupdate/${classId}`, body)
  return res
}

export const getClassTeacher = async (classId, body) => {
  const res = await authApi.post(`class/classupdate/${classId}`, body)
  return res
}

export const getTeacherForAdd = async (classId, dojangId) => {
  const res = await authApi.get(`kwanjang/instructorreadexcept/${dojangId}/${classId}`)
  return res.data
}

export const addTeacherToClass = async (dojangId, classId, [instructorType, instructorId]) => {
  const res = await authApi.get(`kwanjang/instructorcreatebytitle/${dojangId}/${classId}/${instructorType}/${instructorId}`)
  return res.data
}

export const createClass = async (body) => {
  const res = await authApi.post('class/classcreate', body)
  return res
}

export const getStudentForAdd = async (classId, dojangId) => {
  const res = await authApi.get(`student/studentreadexcept/${dojangId}/${classId}`)
  return res.data
}

export const addStudentToClass = async (classId, studentId) => {
  const res = await authApi.get(`student/studentcreatebytitle/${classId}/${studentId}`)
  return res.data
}

export const getTeacherForDel = async (classId, dojangId) => {
  const res = await authApi.get(`kwanjang/instructorread/${dojangId}/${classId}`)
  return res.data
}

export const delTeacherToClass = async (classId, [instructorType, instructorId]) => {
  const res = await authApi.get(`kwanjang/instructordeletebytitle/${classId}/${instructorType}/${instructorId}`)
  return res.data
}

export const getStudentForDel = async (classId, dojangId) => {
  const res = await authApi.get(`student/studentreadbyclass/${dojangId}/${classId}`)
  return res.data.rows
}

export const delStudentToClass = async (classId, studentId) => {
  const res = await authApi.get(`student/studentdeletebytitle/${classId}/${studentId}`)
  return res
}





