import React, { useState } from 'react'
import ArrowForwardIosIcon from '@mui/icons-material/ArrowForwardIos';
import { useModal } from '../../../hooks/basic/useModal';
import { styled } from '@mui/system';
import { Box as MUIBox } from '@mui/material'
import { useMutation } from '@tanstack/react-query';
import ModalContainerSub from '../../layout/ModalContainerSub';
import ProfileImage from '../../UI/Atoms/Profile/ProfileImage';
import { getStudentInfo } from '../../../api/Student/studentApi';
import Box from '../../layout/Box';
import InfoLabelSM from '../../UI/Atoms/Info/InfoLabelSM';
import InfoLabelMD from '../../UI/Atoms/Info/InfoLabelMD';
import InfoData from '../../UI/Atoms/Info/InfoData';
import { phoneHyphenRegex } from '../../../utils/lib/Regex';

const StyledBox = styled(MUIBox)(({ theme }) => ({
  display: 'flex',
  gap: '10px',
  border: '1px solid #dddddd',
  borderRadius: '5px',
  height: '50px',
  backgroundColor: theme.palette.background.secondary,
  alignItems: 'center',
  padding: '0 10px',
  cursor: 'pointer',
  '&:hover': {
    backgroundColor: theme.button.box.hover,
    boxShadow: '0px 4px 4px rgba(0, 0, 0, 0.25)',
  },
  '&:active': {
    backgroundColor: theme.button.box.active,
    boxShadow: '0px 1px 1px rgba(0, 0, 0, 0.25)',
  },
}));



const StudentPop = ({ info }) => {
  const studentId = info?.ClassStudents?.student || ''
  const [studentInfo, setStudentInfo] = useState('')

  // custom hook
  const [open, openFn, closeFn] = useModal()

  // query
  const studnet = useMutation((studentId) => getStudentInfo(studentId))

  // handler
  const handleClick = (e) => {
    studnet.mutateAsync(studentId, {
      onSuccess: (res) => {
        setStudentInfo(res)
        openFn()
      }
    })
  }

  return (
    <>
      <StyledBox onClick={handleClick}>
        <ProfileImage urlKey={info?.photo_url} />
        <div className="text-[16px] w-[162px]">
          {info?.last_name}
          {info?.first_name}
        </div>
        <div className="flex jsutify-center items-center">
          <ArrowForwardIosIcon fontSize="10px" className="text-[#555555]" />
        </div>
      </StyledBox>

      <ModalContainerSub open={open} onClose={() => closeFn()}>
        <div className="text-[22px] font-bold">수련생 정보</div>
        <div className="flex flex-col items-center mt-[30px]">
          <ProfileImage size="140px" urlKey={studentInfo?.photo_url} />
          <div className="mt-[10px] font-bold text-[18px] h-[30px]">
            {studentInfo?.last_name}
            {studentInfo?.first_name}
          </div>
          <div className="text-[#00196a] mb-[2px] h-[25px]">{studentInfo?.dob}</div>
        </div>
        <Box className="mt-[10px] pt-[0]">
          <div className="flex items-center mb-[5px] h-[30px]">
            <InfoLabelMD className="text-[16px]">소속반</InfoLabelMD>
            <InfoData>{studentInfo ? studentInfo?.Classes[0]?.title : ''}</InfoData>
          </div>
          <div className="flex items-center mb-[5px] h-[30px]">
            <InfoLabelMD className="text-[16px]">전화번호</InfoLabelMD>
            <InfoData>{phoneHyphenRegex(studentInfo?.phone_number)}</InfoData>
          </div>
          <div className="flex items-center mb-[5px] h-[30px]">
            <InfoLabelMD className="text-[16px]">단/급 정보</InfoLabelMD>
            <InfoData>{studentInfo?.level}</InfoData>
          </div>
          <div className="flex items-center mb-[5px] h-[30px]">
            <InfoLabelMD className="text-[16px]">보호자</InfoLabelMD>
            <InfoData>
              {studentInfo?.Rep_name} {studentInfo?.Rep_rel ? `(${studentInfo?.Rep_rel})` : ''}
            </InfoData>
          </div>
          <div className="flex items-center mb-[5px] h-[30px]">
            <InfoLabelMD className="text-[16px]">보호자 연락처</InfoLabelMD>
            <InfoData>{phoneHyphenRegex(studentInfo?.Rep_phone_number)}</InfoData>
          </div>
        </Box>
      </ModalContainerSub>
    </>
  );
}

export default StudentPop