import { useMutation, useQuery } from '@tanstack/react-query'
import React, { useState } from 'react'
import { useRecoilValue } from 'recoil'
import { addStudentToClass, delStudentToClass, getStudentForAdd, getStudentForDel } from '../../../../../../api/Class/ClassApi'
import StudentPop from '../../../../../../components/common/Student/StudentPop'
import PopWrap from '../../../../../../components/common/Wrap/PopWrap'
import Box from '../../../../../../components/layout/Box'
import ModalContainer from '../../../../../../components/layout/ModalContainer'
import BlackBtnSM from '../../../../../../components/UI/Atoms/Button/BlackBtnSM'
import WhiteBtnSM from '../../../../../../components/UI/Atoms/Button/WhiteBtnSM'
import EditBtnSM from '../../../../../../components/UI/Atoms/Button/EditBtnSM'
import ProfileImage from '../../../../../../components/UI/Atoms/Profile/ProfileImage'
import { useDebug } from '../../../../../../hooks/basic/useDebug'
import { useModal } from '../../../../../../hooks/basic/useModal'
import { useChecks } from '../../../../../../hooks/input/useChecks'
import dojangIdAtom from '../../../../../../recoil/atom/dojangIdAtom'
import { toGender } from '../../../../../../utils/render/toGender'
import { classInfoAtom } from '../../recoil/classInfoAtom'
import { Grid } from '../../../../../../components/common/Grid/Grid'
import Description from '../../../../../../components/UI/Atoms/Ex/Description'
import Em from '../../../../../../components/UI/Atoms/Ex/Em'
import { toAge } from '../../../../../../utils/render/toAge'
import { phoneHyphenRegex } from '../../../../../../utils/lib/Regex'

const StudentsInfo = () => {
  // recoil
  const dojangId = useRecoilValue(dojangIdAtom)
  const classInfo = useRecoilValue(classInfoAtom)
  const { id: classId } = classInfo
  
  // state
  const [openAdd, openAddFn, closeAddFn] = useModal()
  const [openDel, openDelFn, closeDelFn] = useModal()
  const [addData, setAddData] = useState([])
  const [delData, setDelData] = useState([])
  
  // custom hook
  const [ addCheckList, setAddCheckList, handleAddCheck ] = useChecks()
  const [ delCheckList, setDelCheckList, handleDelCheck ] = useChecks()

  // query - add
  const listForAdd = useMutation(() => getStudentForAdd(classId, dojangId))
  const { mutate: addStudentMutate, isLoading: addLoading } = useMutation((studentId) => addStudentToClass(classId, studentId))
  // query - del
  const listForDel = useMutation(() => getStudentForDel(classId, dojangId))
  const { mutate: delStudentMutate, isLoading: delLoading } = useMutation((studentId) => delStudentToClass(classId, studentId))
  
  // handler
  const handleAdd = () => {
    listForAdd.mutate(classId, {
      onSuccess: async (res) => {
        setAddData(res)
        let arr = new Array(res.length).fill(false)
        setAddCheckList([...arr])
        await openAddFn()
      }
    })
  }

  const handleAddSubmit = () => {
    addCheckList.forEach( (v, i) => {
      if (v) {
        let data = addData[i]
        let studentId = data.id
        addStudentMutate(studentId, {
          onSuccess: () => window.location.reload()
        })
      }
    })
  }
  
  const handleDel = () => {
    listForDel.mutate(classId, {
      onSuccess: async (res) => {
        setDelData(res)
        let arr = new Array(res.length).fill(false)
        setDelCheckList([...arr])
        await openDelFn()
      }
    })
  }
  
  const handleDelSubmit = () => {
    delCheckList.forEach( (v, i) => {
      if (v) {
        let data = delData[i]
        let studentId = data.id
        delStudentMutate(studentId, {
          onSuccess: () => window.location.reload()
        })
      }
    })
  }

  // useDebug(delData)
  return (
    <>
    <Box
      title={`소속 수련생 (${classInfo?.StudentInfos?.length || '0'}명)`}
      btn={
        <div className='flex gap-[5px]'>
          <WhiteBtnSM onClick={handleDel}>삭제</WhiteBtnSM>
          <BlackBtnSM onClick={handleAdd}>추가</BlackBtnSM>
        </div>
      }
    >
      <PopWrap>
        { React.Children.toArray(
          classInfo?.StudentInfos?.map( item =>
            <StudentPop info={item} />
          )
        )}
      </PopWrap>
      <ModalContainer
        open={openAdd}
        onClose={()=>{closeAddFn(); setAddData([])}}
        title="소속 수련생 추가"
      >
        <div className='mt-[25px]'>
          <Grid modal>
            <Grid.Head className='px-[20px]'>
              <Description>
                현재 {classInfo?.title}에 소속되어 있지 않은 수련생은 <Em>{addData?.length}명</Em> 입니다.
              </Description>
            </Grid.Head>
            <Grid.RowWrap>
              <Grid.Row className="justify-start gap-1 h-[60px]">
                <Grid.Chk><input id='all' type='checkbox' onChange={handleAddCheck} className="w-[15px] h-[15px]" /></Grid.Chk> 전체 선택
              </Grid.Row>
              { React.Children.toArray(
                addData?.map((item, idx) => 
                  <Grid.Row>
                    <Grid.Chk><input id={`${idx}`} type='checkbox' onChange={handleAddCheck} checked={addCheckList[idx]} className="w-[15px] h-[15px]" /></Grid.Chk>
                    <Grid.Col fixed='75px'><ProfileImage urlKey={item?.photo_url} /></Grid.Col>
                    <Grid.Col fixed='140px' className="font-bold pl-0">{item?.last_name}{item?.first_name}</Grid.Col>
                    <Grid.Col fixed='60px' className='p-0'>{toGender(item?.sex)}</Grid.Col>
                    <Grid.Col fixed='60px' className='p-0'>{toAge(item.dob)}</Grid.Col>
                    <Grid.Col fixed='60px' className='p-0'>{item.level}</Grid.Col>
                    <Grid.Col fixed='130px' className='p-0'>{item?.Classes?.title}</Grid.Col>
                    <Grid.Col fixed='150px'>{phoneHyphenRegex(item?.phone_number)}</Grid.Col>
                  </Grid.Row>
                )
              )}
            </Grid.RowWrap>
          </Grid>
        </div>
        <div className="flex justify-center mt-10">
          <EditBtnSM onClick={handleAddSubmit} disabled={addLoading}>추가</EditBtnSM>
        </div>


      </ModalContainer>

      <ModalContainer
        open={openDel}
        onClose={()=>{closeDelFn(); setDelData([])}}
        title="소속 수련생 삭제"
      >
        <div className='mt-[25px]'>
          <Grid modal>
            <Grid.Head className='px-[20px]'>
              <Description>
                현재 {classInfo?.title}에 소속되어 있는 수련생은 <Em>{delData?.length}명</Em> 입니다.
              </Description>
            </Grid.Head>
            <Grid.RowWrap>
              <Grid.Row className="justify-start gap-1 h-[60px]">
                <Grid.Chk><input id='all' type='checkbox' onChange={handleDelCheck} className="w-[15px] h-[15px]" /></Grid.Chk> 전체 선택
              </Grid.Row>
              { React.Children.toArray(
                delData?.map((item, idx) => 
                  <Grid.Row>
                    <Grid.Chk><input id={`${idx}`} type='checkbox' onChange={handleDelCheck} checked={delCheckList[idx]} className="w-[15px] h-[15px]" /></Grid.Chk>
                    <Grid.Col fixed='75px'><ProfileImage urlKey={item?.photo_url} /></Grid.Col>
                    <Grid.Col fixed='140px' className="font-bold pl-0">{item?.last_name}{item?.first_name}</Grid.Col>
                    <Grid.Col fixed='60px' className='p-0'>{toGender(item?.sex)}</Grid.Col>
                    <Grid.Col fixed='60px' className='p-0'>{toAge(item.dob)}</Grid.Col>
                    <Grid.Col fixed='60px' className='p-0'>{item.level}</Grid.Col>
                    <Grid.Col fixed='130px' className='p-0'>{item?.Classes?.title}</Grid.Col>
                    <Grid.Col fixed='150px'>{phoneHyphenRegex(item?.phone_number)}</Grid.Col>
                  </Grid.Row>
                )
              )}
            </Grid.RowWrap>
          </Grid>
        </div>
        <div className="flex justify-center mt-10">
          <EditBtnSM onClick={handleDelSubmit} disabled={delLoading}>삭제</EditBtnSM>
        </div>

      </ModalContainer>
    </Box>
    </>
  )
}

export default StudentsInfo