import { useState } from "react"

export const useChecks = () => {
  const [checkList, setCheckList] = useState([])

  const handleCheck = (e) => {
    const key = e.target.id
    const check = e.target.checked

    if (key==='all') {
      if (check) {
        let temp = checkList.fill(true)
        setCheckList([...temp])
      } else {
        let temp = checkList.fill(false)
        setCheckList([...temp])
      }
    } else {
      if (check) {
        const temp = checkList
        temp[key] = true
        setCheckList([...temp])
      } else {
        const temp = checkList
        temp[key] = false
        setCheckList([...temp])
      }
    }
  }

  return [ checkList, setCheckList, handleCheck ]
}