export function dayOfWeek(day, time) {
  if (!time) {
    return (
      <span className="w-[28px] h-[28px] mx-[2px] px-[6px] py-[3px] rounded-[18px] text-[#555555] border border-solid border-[1px] border-[#dddddd]">
        {day}
      </span>
    );
  }
  return (
    <span className="w-[28px] h-[28px] mx-[2px] px-[6px] py-[3px] rounded-[18px] bg-[#555555] text-[#fff] border border-solid border-[1px] border-[#555555]">
      {day}
    </span>
  );
}
