import { useMutation } from '@tanstack/react-query';
import React, { useState } from 'react';
import { useRecoilValue } from 'recoil';
import { createClass } from '../../../../api/Class/ClassApi';
import ClassSchedule from '../../../../components/common/Table/ClassSchedule';
import Box from '../../../../components/layout/Box';
import EditBtnSM from '../../../../components/UI/Atoms/Button/EditBtnSM';
import InfoLabelSM from '../../../../components/UI/Atoms/Info/InfoLabelSM';
import { useInput } from '../../../../hooks/input/useInput';
import dojangIdAtom from '../../../../recoil/atom/dojangIdAtom';

const CreateClass = () => {
  const dojangId = useRecoilValue(dojangIdAtom);

  // custom hook
  const [title, handleChange] = useInput();
  const [editData, setEditData] = useState({
    mon_time: '',
    tue_time: '',
    wed_time: '',
    thu_time: '',
    fri_time: '',
    sat_time: '',
    sun_time: '',
  });

  // query
  const { isLoading, mutate } = useMutation((body) => createClass(body), {
    onSuccess: () => setTimeout(1000, window.location.reload()),
    onError: () => alert('error!'),
  });

  // handler
  const handleSave = (e) => {
    let body = {
      title: title,
      dojang: dojangId,
      ...editData,
    };
    if (!title) {
      alert('반 이름을 입력해주세요.');
      return;
    }
    if (
      !editData.mon_time &&
      !editData.tue_time &&
      !editData.wed_time &&
      !editData.thu_time &&
      !editData.fri_time &&
      !editData.sat_time &&
      !editData.sun_time
    ) {
      alert('최소 1개 이상의 시간표를 입력해주세요.');
      return;
    }

    mutate(body);
  };

  return (
    <>
      <Box className="mt-[25px]" title="시간표">
        <div className="flex justify-items-start my-[10px]">
          <InfoLabelSM className="w-[88px] flex-none pt-[5px]">반 이름</InfoLabelSM>
          <input
            value={title || ''}
            onChange={handleChange}
            placeholder={'반 이름을 입력해주세요.'}
            className="px-[10px] w-[186px] h-[30px]"
          />
        </div>

        <ClassSchedule data={editData} setData={setEditData} edit={true} />
      </Box>
      <div className="flex justify-center mt-[50px]">
        <EditBtnSM onClick={handleSave} disabled={isLoading}>
          저장
        </EditBtnSM>
      </div>
    </>
  );
};

export default CreateClass;
