import React,{ useState } from 'react'
import { useMutation } from '@tanstack/react-query'
import { useRecoilValue } from 'recoil'
import { deleteClass } from '../../../../../../api/Class/ClassApi'
import AlertText from '../../../../../../components/common/Alert/AlertText'
import ModalContainerAlert from '../../../../../../components/layout/ModalContainerAlert'
import EditBtnSM from '../../../../../../components/UI/Atoms/Button/EditBtnSM'
import { useModal } from '../../../../../../hooks/basic/useModal'
import { classInfoAtom } from '../../recoil/classInfoAtom'
import ScheduleInfo from './ScheduleInfo'
import StudentsInfo from './StudentsInfo'
import TeacherInfo from './TeacherInfo'


const ClassInfo = () => {
  
  // recoil
  const classInfo = useRecoilValue(classInfoAtom)

  const [editData, setEditData] = useState(classInfo)

  // query
  const { delClass } = useMutation((classId) => deleteClass(classId))

  // custom hook
  const [open, openFn, closeFn ] = useModal()

  // handler
  const handleClick = () => {
    openFn()
  }
  const handleConfirm = async () => {
    await delClass.mutateAsync(classInfo?.id)
    window.location.replace('class')
  }

  // useDebug(classInfo)


  return (
    <>
      <ScheduleInfo />
      <TeacherInfo />
      <StudentsInfo />
      <div className='flex justify-end'>
        <EditBtnSM onClick={handleClick}>반 삭제</EditBtnSM>
      </div>

      <ModalContainerAlert
        open={open}
        onClose={() => closeFn()}
        onConfirm={handleConfirm}
        confirmText='삭제'
      >
        <AlertText title>{editData?.title}을 삭제합니다.</AlertText>
        <AlertText>반을 삭제하면 복구가 불가능합니다.<br/>정말 삭제하시겠습니까?</AlertText>
      </ModalContainerAlert>
    </>

    
  )
}

export default ClassInfo