import { useMutation } from '@tanstack/react-query';
import React from 'react';
import { useRecoilValue, useResetRecoilState, useSetRecoilState } from 'recoil';
import { getClassInfo } from '../../../../../api/Class/ClassApi';
import { Grid } from '../../../../../components/common/Grid/Grid';
import ModalContainer from '../../../../../components/layout/ModalContainer';
import ArrowIcon from '../../../../../components/UI/Atoms/Icon/ArrowIcon';
import { useDebug } from '../../../../../hooks/basic/useDebug';
import { useModal } from '../../../../../hooks/basic/useModal';
import dojangIdAtom from '../../../../../recoil/atom/dojangIdAtom';
import { dayOfWeek } from '../../../../../utils/render/dayOfWeek';
import { classInfoList } from '../hooks/classInfoList';
import { classInfoAtom } from '../recoil/classInfoAtom';
import ClassInfo from './ClassInfo.jsx/ClassInfo';

const ClassInfoList = () => {
  const dojangId = useRecoilValue(dojangIdAtom);
  const setClassInfo = useSetRecoilState(classInfoAtom);
  const resetClassInfo = useResetRecoilState(classInfoAtom);

  // custom hook
  const [open, openFn, closeFn] = useModal();

  // query
  const { data: classList, refetch } = classInfoList();
  const classInfo = useMutation((classId) => getClassInfo(classId, dojangId));

  const getInfo = async (classId) => {
    await classInfo.mutateAsync(classId, {
      onSuccess: (res) => {
        setClassInfo(res);
        openFn();
      },
    });
  };

  // handler
  const handleClickItem = (e) => {
    const clickId = e.currentTarget.id;
    getInfo(clickId);
  };

  const closeModal = () => {
    refetch();
    closeFn();
    resetClassInfo();
  };

  // useDebug(classList);
  return (
    <>
      <Grid>
        <Grid.Head className="px-0 text-[17px]">
          <Grid.Col w={3}>반 이름</Grid.Col>
          <Grid.Col w={4}>수업 요일</Grid.Col>
          <Grid.Col w={2}>수련생 수</Grid.Col>
          <Grid.Col w={2}>사부님 수</Grid.Col>
          <Grid.Col w={1}></Grid.Col>
        </Grid.Head>
        <Grid.RowWrap>
          {React.Children.toArray(
            classList?.map((item) => (
              <Grid.Row id={item.id} onClick={handleClickItem}>
                <Grid.Col w={3} className="">
                  {item?.title}
                </Grid.Col>
                <Grid.Col w={4} className="pl-0">
                  {dayOfWeek('월', item?.mon_time)}
                  {dayOfWeek('화', item?.tue_time)}
                  {dayOfWeek('수', item?.wed_time)}
                  {dayOfWeek('목', item?.thu_time)}
                  {dayOfWeek('금', item?.fri_time)}
                  {dayOfWeek('토', item?.sat_time)}
                  {dayOfWeek('일', item?.sun_time)}
                </Grid.Col>
                <Grid.Col w={2} className="pl-0">
                  {item?.student_count || '0'}
                </Grid.Col>
                <Grid.Col w={2} className="mr-[25px]">
                  {item?.sabuCnt || '0'}
                </Grid.Col>
                <Grid.Col>
                  <ArrowIcon />
                </Grid.Col>
              </Grid.Row>
            )),
          )}
        </Grid.RowWrap>
      </Grid>

      <ModalContainer open={open} onClose={() => closeModal()} title="반 정보">
        <ClassInfo />
      </ModalContainer>
    </>
  );
};

export default ClassInfoList;
