import { useMutation, useQuery } from '@tanstack/react-query';
import { set } from 'date-fns/esm';
import React, { useState } from 'react';
import { useRecoilValue } from 'recoil';
import {
  addTeacherToClass,
  delTeacherToClass,
  getTeacherForAdd,
  getTeacherForDel,
} from '../../../../../../api/Class/ClassApi';
import { Grid } from '../../../../../../components/common/Grid/Grid';
import TeacherPop from '../../../../../../components/common/Teacher/TeacherPop';
import PopWrap from '../../../../../../components/common/Wrap/PopWrap';
import Box from '../../../../../../components/layout/Box';
import ModalContainer from '../../../../../../components/layout/ModalContainer';
import BlackBtnSM from '../../../../../../components/UI/Atoms/Button/BlackBtnSM';
import EditBtnSM from '../../../../../../components/UI/Atoms/Button/EditBtnSM';
import WhiteBtnSM from '../../../../../../components/UI/Atoms/Button/WhiteBtnSM';
import Description from '../../../../../../components/UI/Atoms/Ex/Description';
import Em from '../../../../../../components/UI/Atoms/Ex/Em';
import ProfileImage from '../../../../../../components/UI/Atoms/Profile/ProfileImage';
import { useDebug } from '../../../../../../hooks/basic/useDebug';
import { useModal } from '../../../../../../hooks/basic/useModal';
import { useChecks } from '../../../../../../hooks/input/useChecks';
import dojangIdAtom from '../../../../../../recoil/atom/dojangIdAtom';
import { classInfoAtom } from '../../recoil/classInfoAtom';

const TeacherInfo = () => {
  // recoil
  const dojangId = useRecoilValue(dojangIdAtom);
  const classInfo = useRecoilValue(classInfoAtom);
  const { id: classId } = classInfo;

  // state
  const [openAdd, openAddFn, closeAddFn] = useModal();
  const [openDel, openDelFn, closeDelFn] = useModal();
  const [addData, setAddData] = useState([]);
  const [delData, setDelData] = useState([]);
  const [loading, setLoading] = useState(false);

  // custom hook
  const [addCheckList, setAddCheckList, handleAddCheck] = useChecks();
  const [delCheckList, setDelCheckList, handleDelCheck] = useChecks();

  // query - add
  const listForAdd = useMutation(() => getTeacherForAdd(classId, dojangId));
  const addTeacher = useMutation((teacherParams) => addTeacherToClass(dojangId, classId, teacherParams),
  );
  // query - del
  const listForDel = useMutation(() => getTeacherForDel(classId, dojangId));
  const delTeacher = useMutation((teacherParams) => delTeacherToClass(classId, teacherParams));

  // handler
  const handleAdd = () => {
    listForAdd.mutate(classId, {
      onSuccess: async (res) => {
        // console.log(res)
        setAddData(res);
        let arr = new Array(res.length).fill(false);
        setAddCheckList([...arr]);
        await openAddFn();
      },
    });
  };

  const handleAddSubmit = () => {
    addCheckList.forEach(async (v, i) => {
      if (i === 0) {
        setLoading(true);
      }
      if (v) {
        let data = addData[i];
        let teacherType = (data.role == 'kwanjang' ? 'kwanjang' : 'sabum');
        let teacherId = data.id;
          // console.log(teacherType, teacherId)
          await addTeacher.mutateAsync([teacherType, teacherId]);
      }
      if (i === addCheckList.length - 1) {
        setTimeout(window.location.reload(), 2000);
      }
    });
  };

  const handleDel = () => {
    listForDel.mutate(classId, {
      onSuccess: async (res) => {
        // console.log(res)
        setDelData(res);
        let arr = new Array(res.length).fill(false);
        setDelCheckList([...arr]);
        await openDelFn();
      },
    });
  };

  const handleDelSubmit = () => {
    delCheckList.forEach(async (v, i) => {
      if (i === 0) {
        setLoading(true);
      }
      if (v) {
        let data = delData[i];
        let teacherType = (data.role == 'kwanjang' ? 'kwanjang' : 'sabum');
        let teacherId = data.id;
        // console.log(teacherType, teacherId)
        await delTeacher.mutateAsync([teacherType, teacherId]);
      }
      if (i === delCheckList.length - 1) {
        setTimeout(window.location.reload(), 2000);
      }
    });
  };

  // useDebug(addData)

  return (
    <>
      <Box
        title={`담당 사부님 (${classInfo?.Sabums.length}명)`}
        btn={
          <div className="flex gap-[5px]">
            <WhiteBtnSM onClick={handleDel}>삭제</WhiteBtnSM>
            <BlackBtnSM onClick={handleAdd}>추가</BlackBtnSM>
          </div>
        }
      >
        <PopWrap>
          {React.Children.toArray(
            classInfo?.KwanjangInfos?.map((item) => <TeacherPop info={item} kwanjang />),
          )}
          {React.Children.toArray(classInfo?.Sabums?.map((item) => <TeacherPop info={item} />))}
        </PopWrap>
      </Box>

      <ModalContainer
        open={openAdd}
        onClose={() => {
          closeAddFn();
          setAddData([]);
        }}
        title="담당 사부님 추가"
      >
        <div className="mt-[25px]">
          <Grid modal>
            <Grid.Head className="px-[20px]">
              <Description>
                현재 {classInfo?.title}을 담당하고 있지 않은 사부님은 <Em>{addData?.length}명</Em>{' '}
                입니다.
              </Description>
            </Grid.Head>
            <Grid.RowWrap>
              <Grid.Row className="justify-start gap-1 h-[60px]">
                <Grid.Chk>
                  <input
                    id="all"
                    type="checkbox"
                    onChange={handleAddCheck}
                    className="w-[15px] h-[15px]"
                  />
                </Grid.Chk>{' '}
                전체 선택
              </Grid.Row>
              {React.Children.toArray(
                addData?.map((item, idx) => (
                  <Grid.Row>
                    <Grid.Chk>
                      <input
                        id={`${idx}`}
                        type="checkbox"
                        onChange={handleAddCheck}
                        checked={addCheckList[idx]}
                        className="w-[15px] h-[15px]"
                      />
                    </Grid.Chk>
                    <Grid.Col w={1}>
                      <ProfileImage urlKey={item?.photo_url} />
                    </Grid.Col>
                    <Grid.Col w={3} className="font-bold pl-0">
                      {item?.last_name}
                      {item?.first_name}
                    </Grid.Col>
                    <Grid.Col w={7}>
                      {item.Classes &&
                        item.Classes?.map((v, idx) => {
                          {
                            if (idx === 0) {
                              return `${v?.title}`;
                            } else {
                              return `, ${v?.title}`;
                            }
                          }
                        })}
                    </Grid.Col>
                  </Grid.Row>
                )),
              )}
            </Grid.RowWrap>
          </Grid>
        </div>
        <div className="flex justify-center mt-10">
          <EditBtnSM onClick={handleAddSubmit} disabled={loading}>
            추가
          </EditBtnSM>
        </div>
      </ModalContainer>

      <ModalContainer
        open={openDel}
        onClose={() => {
          closeDelFn();
          setDelData([]);
        }}
        title="담당 사부님 삭제"
      >
        <div className="mt-[25px]">
          <Grid modal>
            <Grid.Head className="px-[20px]">
              <Description>
                현재 {classInfo?.title}을 담당하고 있는 사부님은 <Em>{delData?.length}명</Em>{' '}
                입니다.
              </Description>
            </Grid.Head>
            <Grid.RowWrap>
              <Grid.Row className="justify-start gap-1 h-[60px]">
                <Grid.Chk>
                  <input
                    id="all"
                    type="checkbox"
                    onChange={handleDelCheck}
                    className="w-[15px] h-[15px]"
                  />
                </Grid.Chk>{' '}
                전체 선택
              </Grid.Row>
              {React.Children.toArray(
                delData?.map((item, idx) => (
                  <Grid.Row>
                    <Grid.Chk>
                      <input
                        id={`${idx}`}
                        type="checkbox"
                        onChange={handleDelCheck}
                        checked={delCheckList[idx]}
                        className="w-[15px] h-[15px]"
                      />
                    </Grid.Chk>
                    <Grid.Col w={1}>
                      <ProfileImage urlKey={item?.photo_url} />
                    </Grid.Col>
                    <Grid.Col w={3} className="font-bold pl-0">
                      {item?.last_name}
                      {item?.first_name}
                    </Grid.Col>
                    <Grid.Col w={7}>
                      {item.Classes &&
                        item.Classes?.map((v, idx) => {
                          {
                            if (idx === 0) {
                              return `${v?.title}`;
                            } else {
                              return `, ${v?.title}`;
                            }
                          }
                        })}
                    </Grid.Col>
                  </Grid.Row>
                )),
              )}
            </Grid.RowWrap>
          </Grid>
        </div>
        <div className="flex justify-center mt-10">
          <EditBtnSM onClick={handleDelSubmit} disabled={loading}>
            삭제
          </EditBtnSM>
        </div>
      </ModalContainer>
    </>
  );
};

export default TeacherInfo;
