import React from 'react'
import PageContainer from '../../../../components/layout/PageContainer'
import ModalContainer from '../../../../components/layout/ModalContainer'
import CreateBtnMD from '../../../../components/UI/Atoms/Button/CreateBtnMD'
import Description from '../../../../components/UI/Atoms/Ex/Description'
import { useModal } from '../../../../hooks/basic/useModal'
import ClassInfoList from './ClassInfoList/ClassInfoList'
import CreateClass from './CreateClass'
import { classInfoList } from './hooks/classInfoList'

const ManageClassPage = () => {
  // custom hook
  const [ open, openFn, closeFn ] = useModal()


  // handler
  const handleClose = (e) => {
    closeFn()
  }

  return (
    <>
      <PageContainer width="md">
          <div className="flex justify-between items-center mb-3 w-full">
            <Description>
              반을 선택하면 반 삭제, 반 정보 수정, 반 원생 추가/삭제, 반 교사 추가/삭제 등을 하실 수 있습니다.
            </Description>
            <CreateBtnMD onClick={() => openFn()} className='text-[16px]'>+ 반 만들기</CreateBtnMD>
          </div>
          <ClassInfoList />
      </PageContainer>

      <ModalContainer
        open={open}
        onClose={handleClose}
        title='반 만들기'
      >
        <CreateClass />
      </ModalContainer>
    </>
  )
}

export default ManageClassPage